import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import MysteriesRosary from './components/MysteriesRosary.vue'

Vue.config.productionTip = false
Vue.component('prayer', MysteriesRosary)

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
